html {
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

body {
  --main-color: #00acdc;
  margin: 0;
}

.container {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: block;
}

.logo__image {
  width: 100%;
  height: 100%;
}

.nav__list {
  text-transform: uppercase;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  margin-left: 20px;
  list-style: none;
}

.nav__item:hover {
  color: var(--main-color);
}

.nav__item:first-child {
  color: var(--main-color);
  margin-left: 0;
}

.nav__link {
  text-transform: uppercase;
  cursor: pointer;
  align-items: center;
  height: 60px;
  font-size: 12px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  right: 0;
}
